import { Component, useState, useCallback } from 'react';
import {
    LoginSocialFacebook,

} from 'reactjs-social-login';
import {
    FacebookLoginButton
} from 'react-social-login-buttons';

const REDIRECT_URI = 'https://reactapp.taggbox.com/login';
const SocialGoogleLogin = (props) => {
    const [provider, setProvider] = useState('');
    const [profile, setProfile] = useState();

    const onLoginStart = useCallback(() => {
        alert('login start');
    }, []);

    const onLogoutSuccess = useCallback(() => {
        setProfile(null);
        setProvider('');
        alert('logout success');
    }, []);

    const onLogout = useCallback(() => { }, []);
    console.log("provider", provider, profile)
    return (
        <>
            <LoginSocialFacebook
                appId={'5880602745393959'}
                fieldsProfile={
                    'id,first_name,last_name,middle_name,name,name_format,picture,short_name,email,gender'
                }
                onLoginStart={onLoginStart}
                onLogoutSuccess={onLogoutSuccess}
                redirect_uri={REDIRECT_URI}
                onResolve={({ provider, data }) => props.onUpdateData(provider, data)}
                onReject={err => {
                    console.log(err);
                }}
            >
                <FacebookLoginButton />
            </LoginSocialFacebook>
        </>
    );
};

export default SocialGoogleLogin;